import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable(
  {
    providedIn: 'root'
  }
)
export class FarrisSidebarService {

  private isOpenSubject = new Subject<any>();

  constructor() {
  }

  sendIsOpen(message: any) {
    this.isOpenSubject.next(message);
  }

  getIsOpen(): Observable<any> {
    return this.isOpenSubject.asObservable();
  }

}